<!-- 课程查询 -->
<template>
	<div class="container">
		<div class="course-title flex">
			<div style="margin-right: 10px;">课程查询</div>
			<div v-clickOutSide="treeShow">
				<el-input v-model="area.name" readonly @focus="areaSelect" placeholder="请选择学校或教室"></el-input>
				<div class="tree-wrap" v-show="show">
					<el-tree :data="data" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
				</div>
			</div>
		</div>
		<div class="box">
			<div class="flex-between">
				<div class="flex" style="margin: 0 auto;">
					<ul class="type-wrap flex">
						<li v-for="item in type" class="flex">
							<div class="type-lump" :style="{'background':item.color}"></div>
							<div>{{item.text}}</div>
						</li>
					</ul>
				</div>
				<div>
					<order-time @time-change="timeSelected"></order-time>
				</div>
			</div>
			<order-calendar :tableData="courseData" :type="4" :current="time"></order-calendar>
		</div>
	</div>
</template>

<script>
import calendar from '../../../components/calendar'
import time from '../../../components/time-selected'
export default {
    data() {
        return {
            type: [{
                color: '#4C84FF',
                text: '同步课'
            }, {
                color: '#FDA423',
                text: '录播课'
            }],
            courseData: [{
                x: 2,
                y: 2,
                name: '生物组织与细胞力学',
                subject: '生物',
                school: '成都七中',
                author: '龙飞',
                time: '8:40-9:20',
                state: 3,
                type: 1
            }],
            roomOption: [],
            room: '',
            time: new Date(),
            data: [],
            defaultProps: {
                children: 'children',
                label: 'name'
            },
            show:false,
            area:{}
        }
    },
    components: {
        'order-calendar': calendar,
        'order-time': time
    },
    created() {
        this.typeSearch()
        this.courseSearch()
    },
    methods: {
        typeSearch() {
            this.$api.orderCourseRoomSearch().then(res => {
				console.log(res)
                this.data = this.getTree(res.data,0)
            })
        },
        courseSearch() {
            let params = {
                date: this.time
            }
            if(this.area.id != undefined){
                if(this.area.type == 0){
					params.school = this.area.id
                }else{
					params.room = this.area.id
                }
            }
            this.$api.orderCoursesSearch(params).then(res => {
                res.data.map(v => {
                    v.section = v.courseSection
                    v.date = this.getWeekDay(v.courseDate)
                })
                this.courseData = res.data
            })
        },
        getWeekDay(params) {
            let time = new Date(params).getDay()
            return time == 0 ? 7 : time
        },
        timeSelected(value) {
            this.time = value;
            this.courseSearch();
        },
        handleNodeClick(data) {
            this.area = data;
		    this.show = false;
            this.courseSearch();
        },
        getTree(list, pId){
            let re = new Array();
            for (let i = 0; i < list.length; i++) {
			    let parentId = list[i].parentId;
			    if (parentId == pId) {
			        let child = this.getTree(list, list[i].id);
			        if (child.length > 0) {
			            list[i].children = child;
			        }
			        re.push(list[i]);
			    }
            }
            return re;
        },
        treeShow(){
            this.show = false
        },
        areaSelect(){
            this.show = !this.show
        }
    }
}
</script>

<style lang="scss" scoped="scoped">
	.container {
		padding: 0 32px;
		background-color: #F1F3F8;
		min-height: calc(100vh - 60px);

		.box {
			background: rgba(255, 255, 255, 1);
			box-shadow: 0px 5px 20px 0px rgba(23, 38, 71, 0.05);
			border-radius: 6px;
			padding: 22px 24px;
			width: 100%;

			.flex-between {
				align-items: center;
				margin-bottom: 30px;

				ul {
					li {
						align-items: center;
						margin-right: 24px;

						.state-lump {
							width: 16px;
							height: 16px;
							border-radius: 4px;
							margin-right: 12px;
						}

						.type-lump {
							width: 24px;
							height: 16px;
							border-radius: 4px;
							margin-right: 12px;
						}
					}
				}
			}
		}

		.course-title {
			padding-top: 20px;
			margin-bottom: 24px;
			align-items: center;

			div {
				&:nth-child(1) {
					color: #55595C;
					font-weight: bold;
				}
				
				&:nth-child(2){
					position: relative;
				} 
				
				.tree-wrap{
					position: absolute;
					width: 100%;
					z-index: 99;
					border:1px solid #dcdfe6;
					max-height: 260px;
					overflow: auto;
				}
			}
		}
	}
</style>
